import React from "react";
import { useLocation } from "react-router-dom";
import bglogin from "../../../assets/img/register_bg_2.png";

export default function EmailVerify() {
  const { search } = useLocation();
  const searchParams = new URLSearchParams(search);

  const message =
    searchParams.get("success") === "true"
      ? "Email sent successfully. Please check your email for registration link."
      : searchParams.get("success") === "false"
      ? "Email verification failed. Please try again."
      : "Seems like you are lost. Please try again.";

  return (
    <div style={{ background: "#262b40" }}>
      <div
        className="d-flex justify-content-center px-2 align-items-center"
        style={{
          backgroundImage: `url(${bglogin})`,
          height: "100vh",
          width: "100vw",
          backgroundSize: "cover"
        }}
      >
        <div style={{ width: "400px", maxWidth: "100%", color: "orange" }}>
          <h2 style={{ color: "darkorange" }}>Email Verification</h2>
          <p>{message}</p>
        </div>
      </div>
    </div>
  );
}
